.insightsTitle {
    @apply text-6xl font-acumin-pro font-bold mb-5;
}

.insightsSubTitle {
    @apply text-eco-green font-acumin-pro text-base mb-4;
}

.pagesTitle {
    @apply font-acumin-pro text-3xl my-8 font-medium;
}

.urlTitle {
    @apply tracking-widest text-so-small mb-5; 
}

.urls{
    @apply flex flex-row items-center my-2.5;
}

.urlText{
    @apply tracking-wider text-sm;
}

.urlText:hover {
    @apply underline; 
}

