.inputWrapper {
    @apply flex flex-col mb-4;
}

.inputLabel {
    @apply text-eco-white text-sm mb-1 font-acumin-pro;
}

.inputField {
    @apply outline-none text-sm text-white px-2 pt-2 pb-1.5 rounded bg-eco-white bg-opacity-20 font-acumin-pro border-2;
}

.errorMessage {
    @apply text-eco-error font-acumin-pro text-sm mt-2 font-bold;
}