/* Styles here */
.headSpacer1{
    @apply flex items-center mb-6 gap-4 ;    
}

.titleHead{
    @apply flex items-center mb-6;    
}

.titleBody{
    @apply  w-full max-w-sm ;
}

.spaccer1{
    @apply w-2/3 ;
}
.spaccer2{
    @apply w-1/3 ;
}

.content1{
    @apply block text-eco-white mb-1 ;
}

.content2{
    @apply block text-eco-white mb-1 ;
}

.textfont1{
    @apply text-5xl ;
}

.button1{
    @apply bg-eco-purple rounded-full pl-4 pr-4 pt-1 pb-1 mt-1 ;
}
.button2{
    @apply bg-eco-purple rounded-full text-center pl-4 pr-4 pt-1 pb-1 mt-8 w-1/2 ;
}
