.webWrapper {
    @apply py-4 border-t border-t-eco-grey;
}

.webTitle {
    @apply font-acumin-pro text-2xl font-bold text-eco-white;
}

.webContent {
    @apply font-acumin-pro text-eco-grey text-left my-2 mb-8;
}

.webLink {
    @apply font-acumin-pro bg-eco-purple text-sm text-eco-white px-3.5 pt-2 pb-1.5 rounded-2xl my-2;
}