.analyticsTitle {
    @apply text-6xl font-acumin-pro font-bold mb-2;
}

.analyticsSubTitle {
    @apply text-eco-green font-acumin-pro uppercase text-xs font-bold my-5;
}

.statsSubTitle {
    @apply font-acumin-pro text-3xl my-1 font-medium mt-6;
}

.statsDateWrapper {
    @apply flex flex-row justify-between items-center w-48;
}

.statsDate {
    @apply font-acumin-pro text-sm;
}

.valueWrapper {
    @apply  relative w-full h-80 border border-eco-purple rounded my-2 mt-6 bg-[#041433];
}

.valueBadge {
    @apply absolute top-3 left-3 text-eco-grey font-semibold uppercase text-xs;
}

.statsDataWrapper {
    @apply w-fit my-5;
}

.statsSubWrapper {
    @apply flex w-full md:w-fit flex-row flex-wrap md:flex-nowrap justify-between items-center;
}

.statsContent {
    @apply mr-4 mb-3 sm:mr-6 md:mr-8;
}

.dataTitle {
    @apply font-acumin-pro text-[11px] uppercase text-left my-1;
}

.dataValue {
    @apply font-acumin-pro text-3xl font-medium text-left;
}

.webCardWrapper {
    @apply grid gap-7 -mt-24;
    @apply md:grid-cols-2 md:-mt-28;
    @apply lg:grid-cols-3 lg:-mt-32
}

.weblinks {
    @apply flex flex-row items-center text-eco-green text-sm my-1 underline;
}


/* Table  */

.tableWrapper {
    @apply border-collapse border-eco-white w-full mt-10;
}

.tableHeadData {
    @apply border-b border-l-eco-purple uppercase text-[12px] px-2 py-1;
}

.tableData {
    @apply border-b pr-1.5 pt-2 pb-1.5 border-l-eco-purple text-[13px]
}