.actionButton {
    @apply font-acumin-pro rounded-3xl px-6 py-1.5 text-sm text-eco-dark w-fit my-2 hover:animate-pulse duration-300 ease-in-out;
}

.submitButton {
    @apply font-acumin-pro bg-eco-dark px-6 py-1 text-sm rounded-3xl border-2 flex flex-row items-center justify-between w-fit my-2 hover:animate-pulse duration-300 ease-in-out;
}

.detailsButton {
    @apply text-white text-xs px-2 py-1 font-acumin-pro rounded-3xl  hover:animate-pulse duration-300 ease-in-out;
}